.signup_container {
	width: 100%;
	min-height: 100vh;
	background-image: url(https://media.istockphoto.com/photos/fresh-italian-pizza-picture-id1045841884?k=20&m=1045841884&s=170667a&w=0&h=mDe_Am4mlFeg1SaKku2sManHsLo_ks3xGfC-siAceDg=);
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	display: flex;
	align-items: center;
	justify-content: center;
}

.signup_form_container {
	width: 600px;
	height: 400px;
	display: flex;
	border-radius: 10px;
	box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
		0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
}

.left {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-image: url(https://media1.giphy.com/media/XeMNgpqttEIxe8qsMt/giphy.gif);
	background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
}

.left h1 {
	margin-top: 0;
	color: white;
	font-size: 20px;
	align-self: center;
}

.right {
	flex: 2;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color:rgba(223, 155, 185, 0.642);
	border-top-right-radius: 10px;
	border-bottom-right-radius: 10px;
}

.form_container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.form_container h1 {
	font-size: 20px;
	margin-top: 0;
}

.input {
	outline: none;
	border: none;
	width: 340px;
	padding: 15px;
	border-radius: 10px;
	background-color: #edf5f3;
	margin: 5px 0;
	font-size: 12px;
}

.error_msg,
.success_msg {
	width: 370px;
	padding: 15px;
	margin: 5px 0;
	font-size: 10px;
	background-color: #f34646;
	color: white;
	border-radius: 5px;
	text-align: center;
}

.success_msg {
	background-color: #5cdd5c;
}

.white_btn,
.green_btn {
	border: none;
	outline: none;
	padding: 12px 0;
	background-color: white;
	border-radius: 20px;
	width: 180px;
	font-weight: bold;
	font-size: 14px;
	cursor: pointer;
}

.green_btn {
	background-color: #44d1a4dc;
	color: white;
	margin: 10px;
}
@media screen and (max-width: 615px){
	.signup_container {
		width: 100vw;
		height: 100vh;
	}
	.signup_form_container {
		width: 380px;
		height: 430px;
		margin-left: 5px;
	
	}
	.error_msg,
	.success_msg{
          width: 180px;
	}
	.sign_name{
		margin-left: 25px;
		font-size: 16px !important;
	}
	.input {
		width: 120px;
	}
}